<template>
  <ul>
    <li v-for="post in posts">
      <pre>
        <router-link :to="'/post/' + post.id">{{post.title}}</router-link> by {{post.name}} - {{new Date(post.timestamp)}}
      </pre>
    </li>
  </ul>
</template>
<script>
export default{
  data() {
    return {
      posts: []
    }
  },
  async created() {
    var res = await axios.get('/api/posts')

    this.posts = res.data
  },
  
}

</script>

