<template>
  <div>
    <h3>发布文章</h3>
    <input type="text" v-model="title" />
    <textarea  cols="30" rows="10" v-model="content"></textarea>
    <button @click="publish">发布</button>
  </div>
</template>

<script>
  export default {
    methods:{
      async publish(){
        var res = await axios.post('/add-post',{
          title:this.title,
          content:this.content
        })
        var post = res.data
        this.$router.push('/post/'+post.id)
      },
    },
    data(){
      return {
        title:'',
        content:'',
      }
    }
  }
</script> 

